import { Component, OnInit, OnDestroy } from '@angular/core';
import { FormBuilder, FormGroup, FormControl, Validators } from "@angular/forms";
import { CandidateService } from 'app/data/candidate/candidate.service';
import { HttpEvent, HttpEventType, HttpErrorResponse } from '@angular/common/http';
import { StorageService } from 'app/providers/storage/storage.service';
import { Router, ActivatedRoute } from '@angular/router';
import { Subscriber } from 'rxjs';

@Component({
  selector: 'candidate-profile',
  templateUrl: './candidate-profile.component.html',
  styleUrls: ['./candidate-profile.component.css']
})
export class CandidateProfileComponent implements OnInit, OnDestroy {
  private candidateForm: FormGroup;
  private userInfo;
  candidateid = undefined;
  candidateData; 
  private newSkill = '';
  private sub;
  private subCan;
  constructor(public fb: FormBuilder,private candidateService: CandidateService,
    public router: Router, private route: ActivatedRoute, private storageServive: StorageService) { 
      this.userInfo = this.storageServive.get('userInfo');
      this.sub = this.route.params.subscribe(params => {
        this.candidateid = params['id'];
        if(this.candidateid) {
          this.candidateService.fetchCandidate(this.candidateid).subscribe((response: any)=>{
            this.candidateData = response.data;
            this.candidateData.candidateDetails.skills = this.candidateData.candidateDetails.skills ? this.candidateData.candidateDetails.skills : [];
              this.candidateForm = this.fb.group({
                resume_link: new FormControl(this.candidateData.candidateDetails.resume_link, { validators: [Validators.required]}),
                bio: new FormControl(this.candidateData.candidateDetails.bio, { validators: [Validators.required]}),
                skills: new FormControl(this.candidateData.candidateDetails.skills, { validators: [Validators.required]}),
                // company_description: new FormControl(this.candidateData.candidateDetails.company_description, { validators: [Validators.required]}),
                first_name: new FormControl(this.candidateData.userDetails.first_name, { validators: [Validators.required]}),
                last_name: new FormControl(this.candidateData.userDetails.last_name, { validators: [Validators.required]}),
                email: new FormControl(this.candidateData.userDetails.email, { validators: [Validators.required]}),
                phone_number: new FormControl(this.candidateData.userDetails.phone_number, { validators: [Validators.required]}),
                date_of_birth: new FormControl(this.candidateData.userDetails.date_of_birth, { validators: [Validators.required]})
              })
           })
        }
      });
      this.candidateForm = this.fb.group({
        resume_link: new FormControl('', { validators: [Validators.required]}),
        bio: new FormControl('', { validators: [Validators.required]}),
        skills: new FormControl('', { validators: [Validators.required]}),
        // company_description: new FormControl('', { validators: [Validators.required]}),
        first_name: new FormControl('', { validators: [Validators.required]}),
        last_name: new FormControl('', { validators: [Validators.required]}),
        email: new FormControl('', { validators: [Validators.required]}),
        phone_number: new FormControl('', { validators: [Validators.required]}),
        date_of_birth: new FormControl('', { validators: [Validators.required]}),
        role: new FormControl('', { validators: [Validators.required]})
      })
    }

  ngOnInit(): void {
  }

  ngOnDestroy() {
    if(this.sub) {
      this.sub.unsubscribe();
    }
    if(this.subCan) {
      this.subCan.unsubscribe();
    }
  }
  submitForm() {
    let candidateDetails = this.candidateForm.value;
    Object.assign(candidateDetails, {skills: this.candidateData.candidateDetails.skills})
    this.subCan = this.candidateService.updateCandidate(candidateDetails, this.candidateData.candidateDetails._id).subscribe( response => {
      if(response.status == "success") {
        this.router.navigate(['/'+this.userInfo.user.role.toLowerCase()+'/candidates']);
      } else {
        alert('Something went wrong');
      }
    })
  }

  private addSkill(){
    this.candidateData.candidateDetails.skills.push(this.newSkill)
    this.newSkill = '';
  }
  
  private removeSkill(removeSkill){
    this.candidateData.candidateDetails.skills = this.candidateData.candidateDetails.skills.filter((skill)=>{
      if(skill != removeSkill) {
        return true;
      }
    })
  }
}
